.storySection {
  height: 305px;
  padding: 21px 17px 16px 17px;
  background-color: #0f0f0f;
  @media screen and (min-width: 450px) {
      height: 405px;
      padding: 32px 17px 47px 17px;
      
      
    }
  
}
.storyTopThump{
  display: flex;
  align-items: center;
}

.storyTitle {
  font-family: Playfair Display;
  font-size: 32.821px;
  font-style: normal;
  font-weight: 400;
  line-height: 140.1%;
  @media screen and (min-width: 450px) {
      font-size: 49.6px;
  
    }
}

.storyIcom {
  width: 46px;
  height: 76px;
  margin-left: auto;
  @media screen and (min-width: 450px) {
    width: 78px;
      height: 96px;
      margin-left: auto;
        
    }
  
}

.storyText {
  font-family: Playfair Display;
  font-size: 11px;
  font-style: normal;
  font-weight: 400;
  line-height: 140.1%;
  @media screen and (min-width: 450px) {
    font-size: 18.6px;
      
  
    }
}

.storyText:first-of-type {
  margin-top: 15px;
  @media screen and (min-width: 450px) {
      margin-top: 33px;
  
  
    }
}
.storyText + .storyText {
  margin-top: 26px;
  @media screen and (min-width: 450px) {
      margin-top: 40px;
  
  
    }
}
